import React from 'react';

const BlogRoom = () => {
  return (
    <div className="py-10 ">
      <div className="w-full bg-[#E9C46A] bg-opacity-75 px-6 sm:px-16 py-6  shadow-lg flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        {/* Blog Room Title */}
        <div className="text-center sm:text-left">
          <h2 className="text-[#125C3A] text-2xl sm:text-3xl font-bold uppercase tracking-wide">
            Blog Room
          </h2>
        </div>

       {/* Left Arrow - Hidden on mobile */}
<div className="hidden sm:flex items-center justify-center text-white text-6xl ml-5 cursor-pointer  transition">
  <span className="font-bold">&lt;</span>
</div>

        {/* Blog Description */}
        <div className="text-center sm:text-left flex-1 sm:px-5">
          <p className="text-[#191818   ] text-sm sm:text-xl font-semibold leading-snug">
            FROM HORMONES TO HEART HEALTH: EXPERT PHARMACEUTICAL CARE FOR EVERY STAGE OF LIFE
          </p>
        </div>

        {/* Read More Button */}
        <div className="text-center sm:text-left sm:mr-20">
          <button className="bg-[#125C3A] text-white font-bold py-2 px-5 rounded-full shadow-md hover:bg-[#0e4c2f] transition">
            Read More
          </button>
        </div>

       {/* Right Arrow - Hidden on mobile */}
<div className="hidden sm:flex items-center justify-center text-white text-6xl cursor-pointer  transition mr-5">
  <span className="font-bold">&gt;</span>
</div>
      </div>
    </div>
  );
};

export default BlogRoom;
